/* eslint-disable no-useless-escape */
/* eslint-disable react-hooks/exhaustive-deps */
import React from "react";
import { NavLink, useHistory } from "react-router-dom";
import { useFormik } from "formik";
import * as yup from "yup";
import { signUpAction } from "../../Redux/action/mainAction/signUp.action";

export default function SignUp() {
  const history = useHistory();
  const validationSchema = yup.object({
    taiKhoan: yup
      .string()
      .required("* Khng  trng!"),
    matKhau: yup
      .string()
      .min(8, "Mt khu phi t 8 k t tr ln")
      .required("* Khng  trng!"),
    hoTen: yup
      .string()
      .matches(/^[A-Za-z]+([\ A-Za-z]+)*/)
      .required("* Khng  trng!"),
    email: yup
      .string()
      .required("* Khng  trng!")
      .email("* Email Khng hp l"),
    soDt: yup
      .string()
      .required("* Khng  trng!")
      .matches(/^[0-9]+$/),
  });

  const formik = useFormik({
    initialValues: {
      taiKhoan: "",
      matKhau: "",
      email: "",
      soDt: "",
      maNhom: "GP09",
      hoTen: "",
      maLoaiNguoiDung: "KhachHang",
    },
    validationSchema: validationSchema,
    onSubmit: (values) => {
      signUpAction(values,history);
    },
  });
  return (
    <div style={{ marginTop: "60px" }}>
      <div className="signin">
        <div className="sign-wrapper">
          <div className="signin-message">
            <h2>ng k</h2>
          </div>

          <form onSubmit={formik.handleSubmit}>
            <div className="form-item">
              <input
                onChange={formik.handleChange}
                onBlur={formik.handleBlur}
                value={formik.values.hoTen}
                type="text"
                name="hoTen"
                placeholder="H v tn"
              ></input>
              {formik.touched.hoTen && formik.errors.hoTen ? (
                <p className="text-danger text-left">* H tn phi l k t [a -Z]</p>
              ) : null}
            </div>
            <div className="form-item">
              <input
                type="text"
                name="taiKhoan"
                placeholder="Tn ti khon"
                value={formik.values.taiKhoan}
                onChange={formik.handleChange}
                onBlur={formik.handleBlur}
              ></input>
              {formik.touched.taiKhoan && formik.errors.taiKhoan ? (
                <p className="text-danger text-left">
                  {formik.errors.taiKhoan}
                </p>
              ) : null}
            </div>
            <div className="form-item">
              <input
                type="password"
                name="matKhau"
                placeholder="Mt khu"
                value={formik.values.matKhau}
                onChange={formik.handleChange}
                onBlur={formik.handleBlur}
              ></input>
              {formik.touched.matKhau && formik.errors.matKhau ? (
                <p className="text-danger text-left">{formik.errors.matKhau}</p>
              ) : null}
            </div>

            <div className="form-item">
              <input
                onChange={formik.handleChange}
                value={formik.values.email}
                type="email"
                name="email"
                placeholder="Email"
                onBlur={formik.handleBlur}
              ></input>
              {formik.touched.email && formik.errors.email ? (
                <p className="text-danger text-left">{formik.errors.email}</p>
              ) : null}
            </div>

            <div className="form-item">
              <input
                type="text"
                name="soDt"
                placeholder="S in thoi"
                value={formik.values.soDt}
                onChange={formik.handleChange}
                onBlur={formik.handleBlur}
              ></input>
              {formik.touched.soDt && formik.errors.soDt ? (
                <p className="text-danger text-left">* S in thoi phi l k t [0-9]</p>
              ) : null}
            </div>

            <div className="button-panel">
              <input
                type="submit"
                className="button"
                title="Sign Up"
                value="ng k"
              ></input>
            </div>
          </form>
          <NavLink to="/">
            <div className="signin-close"></div>
          </NavLink>
        </div>
      </div>
    </div>
  );
}
