/* eslint-disable no-useless-escape */
export const validate = values => {
    const errors = {}
    if (!values.taiKhoan) {
        errors.taiKhoan = "* Khng  trng!"
    } else if (values.taiKhoan.length > 15) {
        errors.taiKhoan = "* Tn ti khon phi t 15 k t hoc t hn"
    }

    if (!values.hoTen) {
        errors.hoTen = "* Khng  trng!";
    } else if (!(/^[A-Za-z]+([\ A-Za-z]+)*/.test(values.hoTen))) {
        errors.hoTen = "* H tn Khng hp l!";
    } else if (values.hoTen.length > 15) {
        errors.hoTen = 'H tn phi t 15 k t hoc t hn';
    }

    if (!values.soDt) {
        errors.soDt = "* Khng  trng!";
    } else if (!(/^[0-9]+$/.test(values.soDt))) {
        errors.soDt = "* S in thoi Khng hp l!";
    }

    if (!values.email) {
        errors.email = "* Khng  trng!";
    } else if (!(/^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(values.email))) {
        errors.email = "* Email Khng hp l!";
    }

    if (!values.matKhau) {
        errors.matKhau = "* Khng  trng!";
    } else if (values.matKhau.length < 8) {
        errors.matKhau = "* Mt khu phi trn 8 k t!";
    }

    if (!values.maLoaiNguoiDung) {
        errors.maLoaiNguoiDung = "* Chn m loi ngi dng!";
    }

    return errors
}

export const youtube_parser = (url = " ") => {
    var regExp = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#&?]*).*/;
    var match = url.match(regExp);
    return match && match[7].length === 11 ? match[7] : false;
  };