import { mainService } from "../../../service"
import Swal from 'sweetalert2'
export const signUpAction = (userSignUp,history) => {
  mainService.signUpAPI(userSignUp)
    .then((res) => {
      Swal.fire({
        icon: 'success',
        text: 'ng k thnh cng',
      }).then(() => {
        history.goBack();
      })
    })
    .catch((err) => {
      Swal.fire({
        icon: 'error',
        text: err.response.data,
      })
    });
};
